.aboutWraper {
  position: absolute;
  width: 60%;
  height: 100%;
  background-color: #d29032;
  text-align: center;
  overflow: hidden;
  z-index: 0;
  opacity: 0;
}

.aplicarsumirContent {
  animation: sumirContent 1s forwards;
}

@keyframes sumirContent {
  0% {
    height: 0%;
    z-index: 2;
    opacity: 1;
  }
  99% {
    height: 100%;
    z-index: 2;
    opacity: 1;
  }
  100% {
    opacity: 1;
    z-index: 2;
  }
}

.aplicarmostrarContent {
  animation: mostrarContent 1s forwards;
}

@keyframes mostrarContent {
  0% {
    height: 100%;
    z-index: 2;
    opacity: 1;
  }
  99% {
    height: 0%;
    z-index: 2;
    opacity: 1;
  }
  100% {
    opacity: 0;
    z-index: 0;
  }
}

.closeArrowWraper {
  height: 50px;
  color: white;
  text-align: center;
  font-size: 3em;
  cursor: pointer;
}

.closeArrowWraper:hover {
  color: #794c2dff;
}

.aboutWraper h2,
.aboutWraper p {
  color: white;
  padding: 0 100px;
  font-size: 3.5vh;
}
.aboutWraper h2 {
  font-size: 6vh;
}

@media (max-width: 800px) {
  .aboutWraper {
    width: 100vw;
  }

  .aboutWraper h2,
  .aboutWraper p {
    font-size: 2.6vh;
  }
  .aboutWraper h2 {
    font-size: 3.2vh;
  }
}
